import axios from "axios";
import { AxiosInstance, AxiosRequestConfig, AxiosResponse } from "axios";

import { RequestForTicket } from "@/intefaces/models";
import { ReportTicketDto } from "@/intefaces/models";

const apiConfig = {
  withCredentials: false,
  timeout: 30000,
  baseURL: "http://localhost:8081/api",
  responseType: "json",
  headers: {
    common: {
      "Cache-Control": "no-cache, no-store, must-revalidate",
      Pragma: "no-cache",
      Accept: "application/json"
    }
  }
} as AxiosRequestConfig;

export default class Api {
  private readonly client: AxiosInstance = axios.create(apiConfig);

  public async submitTicket(
    /* eslint-disable-next-line @typescript-eslint/camelcase */
    ticket_req: RequestForTicket
  ): Promise<ReportTicketDto | null> {
    /* eslint-disable-next-line @typescript-eslint/camelcase */
    return this.client
      .post<ReportTicketDto>("tickets", ticket_req, {
        headers: {
          "Content-Type": "application/json"
        }
      })
      .then(res => res.data as ReportTicketDto)
      .catch(e => {
        alert(e);
        return null;
      });
  }

  public async getTicketByIdIfCompleted(
    /* eslint-disable-next-line @typescript-eslint/camelcase */
    ticket_id: number
  ): Promise<ReportTicketDto | null> {
    return (
      this.client
        /* eslint-disable-next-line @typescript-eslint/camelcase */
        .get<ReportTicketDto>(`tickets/${ticket_id}`)
        .then(resp =>
          resp.status === 202 ? null : (resp.data as ReportTicketDto)
        )
        .catch(e => {
          alert(e);
          return null;
        })
    );
  }

  public async submitLogFile(formData: FormData): Promise<boolean> {
    return this.client
      .put<boolean>("uploader/log", formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
      .then(() => true)
      .catch(e => {
        alert(e);
        return false;
      });
  }
}
