
































































import Vue, { PropType } from "vue";
import ReportTicket from "@/components/ReportTicket.vue";
import { ReportTicketDto, WinnersReportEntry } from "@/intefaces/models";
import Api from "@/api";

export default Vue.extend({
  name: "TicketBoard",
  components: {
    ReportTicket
  },
  data: () => ({
    api: new Api(),
    selectedTicket: {
      id: -1 as number,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      created_time: "" as string,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      service_domain: "" as string,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      period_start: "" as string,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      period_end: "" as string,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      is_completed: false as boolean,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      report_entries: [] as WinnersReportEntry[]
    } as ReportTicketDto
  }),
  props: {
    tickets: {
      type: Object as PropType<ReportTicketDto[]>
    }
  },
  methods: {
    displayReport(ticket: ReportTicketDto) {
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      if (ticket.report_entries.length === 0) {
        alert("В отчете нет записей");
      } else this.selectedTicket = ticket;
    },
    getIfCompleted: function(ticketId: number) {
      this.api
        .getTicketByIdIfCompleted(ticketId)
        .then((completedTicket: ReportTicketDto | null) => {
          if (!completedTicket) {
            return;
          }

          const tik: ReportTicketDto = this.tickets.find(
            (t: ReportTicketDto) => t.id === ticketId
          ) as ReportTicketDto;
          /* eslint-disable-next-line @typescript-eslint/camelcase */
          tik.is_completed = completedTicket.is_completed;
          /* eslint-disable-next-line @typescript-eslint/camelcase */
          tik.report_entries = completedTicket.report_entries;
        });
    }
  }
});
