

























































































































import Vue from "vue";
import { RequestForTicket } from "src/intefaces/models";

export default Vue.extend({
  name: "Configurator",

  data: () => ({
    requestParams: {
      // eslint-disable-next-line @typescript-eslint/camelcase
      start_from_date: "",
      until: "",
      // eslint-disable-next-line @typescript-eslint/camelcase
      service_domain: "ours.com",
      // eslint-disable-next-line @typescript-eslint/camelcase
      recalculate_if_exists: false
    } as RequestForTicket,
    logFileForUpload: null as File | null,
    servicePicker: ["ours.com", "theirs1.com", "theirs2.com"] as string[],
    periodStartPicker: false as boolean,
    periodEndPicker: false as boolean
  }),
  props: {
    isLogLoading: Boolean
  },
  methods: {
    requestNewTicket: function() {
      this.$emit("api-post-new-ticket", this.requestParams);
    },
    sendLogFileToServer: function() {
      if (this.logFileForUpload === null) {
        alert("Файл не выбран!");
        return;
      }
      const file: File = this.logFileForUpload as File;
      const formData: FormData = new FormData();
      formData.append("file", file, file.name);
      this.$emit("api-send-log-file", formData);
    }
  }
});
