




























import Vue, { PropType } from "vue";
import { ReportTicketDto } from "@/intefaces/models";

export default Vue.extend({
  name: "ReportTicket",
  props: {
    reportTicket: {
      type: Object as PropType<ReportTicketDto>
    }
  },
  methods: {
    checkForCompletion: function() {
      this.$emit("api-check-for-completion", this.reportTicket.id);
    },
    showReport: function() {
      this.$emit("display-report", this.reportTicket);
    }
  }
});
