
























import Vue from "vue";
import Configurator from "./components/Configurator.vue";
import TicketBoard from "@/components/TicketBoard.vue";
import { ReportTicketDto } from "@/intefaces/models";
import { RequestForTicket } from "@/intefaces/models";
import Api from "@/api";

export default Vue.extend({
  name: "App",

  components: {
    Configurator,
    TicketBoard
  },
  props: {
    //
  },
  methods: {
    submitNewTicket: function(request: RequestForTicket) {
      this.api.submitTicket(request).then((ticket: ReportTicketDto | null) => {
        if (!ticket) {
          return;
        }
        this.tickets.push(ticket as ReportTicketDto);
      });
    },
    uploadLogFile: function(data: FormData) {
      this.isLogLoading = true;
      this.api
        .submitLogFile(data)
        .then(m => m)
        .catch(e => alert(e));
      this.isLogLoading = false;
    }
  },

  data: () => ({
    api: new Api(),
    tickets: [] as ReportTicketDto[],
    isLogLoading: false as boolean
  })
});
